import {getPositions, getToken, registerUser} from "../api/api";
import {updateUsers} from "./usersRuducer";
import {stopSubmit, reset} from "redux-form";

const initialState = {
    positions: [],
    error: null,
    isShowModal: false,
    isRegInProgress: false,
    fileName: 'Upload your photo',
};

const SET_POSITIONS = 'formSectionReducer/SET_POSITIONS';
const SET_ERROR = 'formSectionReducer/SET_ERROR';
const SHOW_MODAL = 'formSectionReducer/SHOW_MODAL';
const SET_REG_IN_PROGRESS = 'formSectionReducer/SET_REG_IN_PROGRESS';
const SET_FILE_NAME = 'formSectionReducer/SET_FILE_NAME';

const formSectionReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_POSITIONS: {
            return {
                ...state,
                positions: action.payload,
            }
        }
        case SET_ERROR: {
            return {
                ...state,
                error: action.payload,
            }
        }
        case SHOW_MODAL: {
            return {
                ...state,
                isShowModal: action.payload
            }
        }
        case SET_REG_IN_PROGRESS: {
            return {
                ...state,
                isRegInProgress: action.payload,
            }
        }
        case SET_FILE_NAME: {
            return {
                ...state,
                fileName: action.payload,
            }
        }
        default: return state;
    }
};

const setPositions = (payload) => ({
    type: SET_POSITIONS,
    payload,
});

const setError = (payload) => ({
    type: SET_ERROR,
    payload
});

export const showModal = (payload) => ({
    type: SHOW_MODAL,
    payload,
});

const setRegInProgress = (payload) => ({
    type: SET_REG_IN_PROGRESS,
    payload,
});

export const setFileName = (payload) => ({
    type: SET_FILE_NAME,
    payload,
});

export const setPositionsAsyncAction = () => {
    return async (dispatch) => {
        try {
            const response = await getPositions();
            dispatch(setPositions(response.positions));
        }
        catch (e) {
            console.log(e);
        }
    }
};

export const registerUserAsyncAction = (name, email, phone, position, photo) => {
    return async (dispatch) => {
        dispatch(setRegInProgress(true));
        try {
            const tokenResponse = await getToken();
            if(tokenResponse.success) {
                const response = await registerUser(name, email, phone, position, photo, tokenResponse.token);
                if (response.data.success) {
                    dispatch(updateUsers());
                    dispatch(showModal(true));
                    dispatch(reset('regForm'));
                    dispatch(setRegInProgress(false));
                    dispatch(setFileName('Upload your photo'));
                }
            }
        } catch (e) {
            dispatch(setRegInProgress(false));
            if(e.response){
                switch (e.response.status) {
                    case 422: {
                        const fails = e.response.data.fails;
                        const errorValues = Object.keys(fails).map(fail => fails[fail][0]);
                        const errors = {};
                        Object.keys(fails).forEach((key, i) => errors[key] = errorValues[i]);
                        const action = stopSubmit('regForm', errors);
                        dispatch(action);
                        break;
                    }
                    case 409: {
                        dispatch(setError(e.response.data.message));
                        break;
                    }
                    default: dispatch(setError(e.message));
                }
            }
            else dispatch(setError(e.message));

        }
    }
};

export default formSectionReducer;