import React from 'react';
import {useDispatch, useSelector} from "react-redux";
import ScrollableAnchor from "react-scrollable-anchor";

import styles from './Users.module.css'
import buttonStyles from '../../styles/Buttons.module.css'
import textStyles  from '../../styles/Text.module.css'

import {getUsersAsyncAction, setNextPage} from "../../redux/usersRuducer";
import {
    getError,
    getIsFetchingUsers, getIsNeedFetchUsers,
    getIsNextPage,
    getPage,
    getUsers
} from "../../redux/usersSelector";
import User from "./User/User";
import useWindowDimensions from "../../hooks/useWindowDimensions";
import Loader from "../../helpers/Loader";

import {Container, Button, Grid} from "@material-ui/core";



const Users = () => {

    const { width } = useWindowDimensions();

    const dispatch = useDispatch();
    const {users, page, isNextPage, isFetchingUsers, error, isNeedFetchUsers} = useSelector(state => ({
        users: getUsers(state),
        page: getPage(state),
        isNextPage: getIsNextPage(state),
        isFetchingUsers: getIsFetchingUsers(state),
        error: getError(state),
        isNeedFetchUsers: getIsNeedFetchUsers(state),
    }));

    React.useEffect(() => {
        const isMobile = width < 480;
        const pageSize =  isMobile ? 3 : 6;
        if(isNeedFetchUsers) dispatch(getUsersAsyncAction(page, pageSize));
    }, [dispatch, page, isNeedFetchUsers]);


    const showMoreButtonHandler = () => {
        dispatch(setNextPage());
    };

    return (
        <section className={styles.wrapper}>
            <Container className={styles.container}>
                <ScrollableAnchor id={'users'}>
                    <h2 className={textStyles.title}>Our cheerful users</h2>
                </ScrollableAnchor>
                <p className={textStyles.subtitle} >Attention! Sorting users by registration date</p>
                {users && users.length > 0 ?
                    <Grid container className={styles.users}>
                        {users.map(user => {
                            return <Grid item lg={4} xl={4} sm={4} xs={12}  key={user.id} className={styles.item}>
                                <User
                                    email={user.email}
                                    name={user.name}
                                    position={user.position}
                                    photo={user.photo}
                                    phone={user.phone}
                                />
                            </Grid>
                        })}
                    </Grid> : <Loader/>}
                {isNextPage && <div className={styles.buttonContainer}>
                    <Button
                        disabled={isFetchingUsers}
                        className={`${buttonStyles.button} ${buttonStyles.buttonContained}`}
                        fullWidth
                        onClick={showMoreButtonHandler}
                    >
                        Show more
                    </Button>
                    {error && <div className={styles.error}>{error}</div>}
                </div>}
            </Container>
        </section>
    );
};

export default Users;