import React from "react";

import StyledRadio from "./StyledRadio";

import styles from "../FormSection.module.css";

import {FormControl, RadioGroup, FormControlLabel} from "@material-ui/core";


const RadioButtons = ({ input, positions, ...rest }) => {

    return <FormControl required className={styles.formControl}>
        <label>Select your position</label>
        <RadioGroup className={styles.radioGroup} {...input} {...rest}>
            {positions.map((position, index) => {
                return <FormControlLabel
                    key={index}
                    value={`${index + 1}`}
                    control={<StyledRadio/>}
                    className={styles.radioBtn}
                    label={<p className={styles.formControlLabel}>{position.name}</p>}
                />
            })}
        </RadioGroup>
    </FormControl>
};

export default RadioButtons;