import React from 'react';
import {goToTop} from "react-scrollable-anchor";

import logo from '../../assets/logo.svg'
import MobileNavBar from "../NavBar/MobileNavBar";
import DesctopNavBar from "../NavBar/DesktopNavBar";

import styles from './Header.module.css'

import {AppBar, Link, Grid , Hidden, Container} from '@material-ui/core'

const menuItems = [
    {name: 'About me', link: '#aboutme'},
    {name: 'Relationships', link: '#'},
    {name: 'Requirements', link: '#'},
    {name: 'Users', link: '#users'},
    {name: 'Sign Up', link: '#signup'}
];

const Header = () => {

    const [activeItem, setActiveItem] = React.useState(null);

    const onLogoClickHandler = () => {
      goToTop();
      setActiveItem(null);
    };

    return (
        <AppBar color={'inherit'}>
            <Container>
                <Grid container className={styles.content}>
                    <Grid item xs={5}><Link onClick={onLogoClickHandler}><img className={styles.logo} alt={'logo'} src={logo}/></Link></Grid>
                    <Grid item xs={7}>
                        <Hidden mdUp><MobileNavBar activeItem={activeItem} setActiveItem={setActiveItem} menuItems={menuItems} logoStyle={styles.logo}/></Hidden>
                        <Hidden smDown><DesctopNavBar activeItem={activeItem} setActiveItem={setActiveItem}  menuItems={menuItems}/></Hidden>
                    </Grid>
                </Grid>
            </Container>
        </AppBar>
    );
};

export default Header;